'use client';

import React, { useState } from 'react';
import { supabase } from '@/lib/supabase';
import { useRouter } from 'next/navigation';
import { ShieldCheck, ArrowRight, Loader2, Key } from 'lucide-center';
import Link from 'next/link';

export default function LoginPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const router = useRouter();

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      // 1. Proses Sign In
      const { data: { user }, error: authError } = await supabase.auth.signInWithPassword({
        email,
        password,
      });

      if (authError) throw authError;

      // 2. Cek Status Pro di Database
      if (user) {
        const { data: profile } = await supabase
          .from('profiles')
          .select('is_pro')
          .eq('id', user.id)
          .single();

        // 3. Redireksi Berdasarkan Status
        if (profile?.is_pro) {
          router.push('/intelligence'); // Langsung ke fitur utama jika Pro
        } else {
          router.push('/dashboard'); // Ke Command Center jika Standar
        }
      }
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-[#FDFCFB] flex items-center justify-center px-8 py-20 font-sans text-black">
      <div className="max-w-md w-full space-y-12">
        <div className="text-center space-y-4">
          <div className="inline-flex items-center gap-2 px-4 py-1.5 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.3em] mb-4">
            <Key className="w-4 h-4" /> Secure Entry
          </div>
          <h1 className="text-4xl font-serif italic leading-tight">Access Your <br/><span className="text-[#D4AF37]">Vault.</span></h1>
        </div>

        <form onSubmit={handleLogin} className="bg-white border border-gray-100 p-10 rounded-[3rem] shadow-2xl shadow-gray-100/50 space-y-8">
          {error && (
            <div className="p-4 bg-red-50 text-red-500 text-[13px] rounded-xl border border-red-100 italic">
              {error}
            </div>
          )}

          <div className="space-y-6">
            <div className="space-y-2">
              <label className="text-[14px] font-black uppercase tracking-widest text-gray-400 ml-2">Email</label>
              <input type="email" required value={email} onChange={(e) => setEmail(e.target.value)} className="w-full bg-[#FDFCFB] border border-gray-100 rounded-2xl py-4 px-6 text-[16px] focus:outline-none focus:border-[#D4AF37]" placeholder="name@company.com" />
            </div>

            <div className="space-y-2">
              <label className="text-[14px] font-black uppercase tracking-widest text-gray-400 ml-2">Password</label>
              <input type="password" required value={password} onChange={(e) => setPassword(e.target.value)} className="w-full bg-[#FDFCFB] border border-gray-100 rounded-2xl py-4 px-6 text-[16px] focus:outline-none focus:border-[#D4AF37]" placeholder="••••••••" />
            </div>
          </div>

          <button type="submit" disabled={loading} className="w-full py-5 bg-black text-[#D4AF37] rounded-2xl text-[14px] font-black uppercase tracking-[0.2em] hover:bg-[#D4AF37] hover:text-black transition-all shadow-xl flex items-center justify-center gap-4">
            {loading ? <Loader2 className="animate-spin w-5 h-5" /> : <>Sign In To Lab <ArrowRight size={18} /></>}
          </button>
        </form>

        <p className="text-center text-[14px] text-gray-400 font-light">
          New to the lab? <Link href="/auth/register" className="text-black font-bold hover:text-[#D4AF37] underline transition-colors">Create Account</Link>
        </p>
      </div>
    </div>
  );
}